html, body {
  width: 100%;
  height: 100%;
}
body{
	background:#fff;
}

.tuiguang_shucai {
  height: .16rem;
  padding: .15rem;
}

.tuiguang_shucai img {
  display: block;
  width: .7rem;
  height: .16rem;
}

.mengcheng {
  font-size: .12rem;
  padding: .15rem;
}

.mengcheng .shucai_name .shucai_name1 {
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.mengcheng .shucai_name .shucai_name2 {
  font-size: .1rem;
  color: #333;
  font-weight: bold;
}

.mengcheng .shucai_ID {
  margin-top: .1rem;
}

.mengcheng .shucai_ID .shucai_id1 {
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.mengcheng .shucai_ID .shucai_id2 {
  font-size: .1rem;
  color: #333;
  font-weight: bold;
}

.tuiguangs_header {
  height: .16rem;
  padding: .15rem 0 0 .15rem;
}

.tuiguangs_header img {
  display: block;
  width: .7rem;
  height: .16rem;
}

.tuiguans_qudao {
  padding: .15rem .15rem .15rem .2rem;
  border-bottom: 1px solid #ddd;
}

.tuiguans_qudao .tuiguangs_filter {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin-top: .2rem;
  height: .39rem;
  line-height: .39rem;
  font-size: .12rem;
  color: #999;
  font-weight: bold;
}

.tuiguans_qudao .tuiguangs_filter li:nth-child(1) {
  overflow: hidden;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
}

.tuiguans_qudao .tuiguangs_filter li:nth-child(1) input {
  font-size: .12rem;
  float: left;
  margin: .11rem .06rem 0 0;
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.tuiguans_qudao .tuiguangs_filter li:nth-child(1) span {
  font-size: .12rem;
  float: left;
  marin-left:.04rem;
  font-size: .12rem;
  color: #999;
  font-weight: bold;
}

.tuiguans_qudao .tuiguangs_filter li:nth-child(2) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
}

.tuiguans_qudao .tuiguangs_filter li:nth-child(3) {
  -webkit-box-flex: 2;
  -webkit-flex: 2;
  flex: 2;
  width: .1px;
  text-align: center;
}

.tuiguang_list {
  padding: 0 .15rem 0 .2rem;
}

.tuiguang_list .tuiguang_list1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  height: .5rem;
  border-bottom: 1px solid #ddd;
  line-height: .5rem;
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.tuiguang_list .tuiguang_list1 li {
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.tuiguang_list .tuiguang_list1 li:nth-child(1) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
}

.tuiguang_list .tuiguang_list1 li:nth-child(2) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
}

.tuiguang_list .tuiguang_list1 li:nth-child(3) {
  -webkit-box-flex: 2;
  -webkit-flex: 2;
  flex: 2;
  width: .1px;
  text-align: center;
}

.tuiguang_list .tuiguang_list1 li input {
  display: block;
  margin-top: .18rem;
}

.tijiaos .but {
  display: block;
  position:absolute;
  left:0;
  bottom:0;
  width:100%;
  height: .5rem;
  line-height:.5rem;
  background: #3399dd;
  outline: none;
  color: #fff;
  font-size:.2rem;
  letter-spacing: .1rem;
  font-weight: bold;
}